export default {
  props: {
    linkDetails: Object,
    formatCurrency: Function,
  },
  computed: {
    noDiscount() {
      if(!this.linkDetails.discount)
        return true;
      return this.linkDetails.discount.amount === 0;
    },
    noShippingCharges() {
      return this.linkDetails.courierCharges === 0;
    }
  }
}
